header {
  background-color: #1a1a1a;
  border-bottom: 1px solid #333;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  display: flex;
  box-shadow: 0 4px 8px #0003;
}

header h1 {
  color: var(--title-color);
  text-shadow: 0 0 8px var(--title-color);
  margin: 0;
  font-size: 24px;
}

.main-tabs {
  background-color: var(--secondary-background);
  border-bottom: 1px solid var(--border-color);
  justify-content: space-around;
  padding: 10px;
  display: flex;
}

.main-tab {
  cursor: pointer;
  color: var(--dropdown-text-color);
  padding: 10px 20px;
  transition: color .3s, border-bottom .3s;
}

.main-tab:hover {
  color: #0c9;
}

.active-main-tab {
  border-bottom: 2px solid var(--dropdown-text-color);
  font-weight: bold;
}

.tabs {
  background-color: var(--secondary-background);
  border-bottom: 1px solid var(--border-color);
  justify-content: space-around;
  padding: 10px;
  display: flex;
}

.tab {
  cursor: pointer;
  color: var(--dropdown-text-color);
  border-radius: 5px;
  transition: color .3s, border-bottom .3s;
}

.tab:hover {
  color: #0c9;
}

.active-tab {
  border-bottom: 2px solid var(--dropdown-text-color);
  font-weight: bold;
}

.tab-content input[type="color"] {
  border-radius: 0;
  width: 20px;
  height: 20px;
}

.content {
  background-color: #1a1a1a;
  border-top: 1px solid #333;
  padding: 20px;
  box-shadow: 0 -4px 8px #0003;
}

h2 {
  color: #0fc;
  text-shadow: 0 0 8px #0fc;
}

p {
  color: #ccc;
}

.hauling-missions {
  background-color: #1a1a1a;
  border: 1px solid #333;
  border-radius: 10px;
  flex-direction: column;
  gap: 10px;
  padding: 20px;
  display: flex;
  box-shadow: 0 4px 8px #0003;
}

.form-group {
  flex-direction: column;
  align-items: center;
  gap: 5px;
  display: flex;
}

.form-group label:not(.preferences label) {
  color: var(--dropdown-label-color);
  text-shadow: 0 0 8px var(--dropdown-label-color);
  text-align: center;
}

.form-group label[for="location-type"] {
  white-space: nowrap;
}

.form-row {
  align-items: center;
  gap: 10px;
  display: flex;
}

.button-group {
  background-color: #1a1a1a;
  border: 1px solid #333;
  border-radius: 10px;
  flex-flow: wrap;
  align-items: center;
  gap: 10px;
  width: 700px;
  margin-top: 20px;
  margin-left: 10px;
  padding: 10px;
  display: flex;
  box-shadow: 0 4px 8px #0003;
}

.button-group button {
  background-color: var(--button-color);
  color: #0d0d0d;
  cursor: pointer;
  white-space: nowrap;
  border: none;
  border-radius: 5px;
  padding: 7px 20px;
  font-size: 16px;
  transition: background-color .3s, color .3s;
}

.button-group button:hover {
  background-color: #0c9;
}

.clear-log-button {
  background-color: #f44 !important;
}

.clear-log-button:hover {
  background-color: #f66 !important;
}

.remove-cargo-button {
  background-color: #f44 !important;
}

.remove-cargo-button:hover {
  background-color: #f66 !important;
}

.add-entry-button, .table-view-button {
  flex-shrink: 0;
}

.actions {
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
  gap: 3px;
  min-width: max-content;
  display: flex;
}

.actions button {
  background-color: var(--button-color);
  color: #0d0d0d;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  height: 24px;
  padding: 3px 8px;
  font-size: 12px;
  transition: background-color .3s, color .3s;
}

.actions button:hover {
  background-color: #0c9;
}

.actions .form-group {
  flex-direction: row;
  align-items: center;
  gap: 5px;
}

.actions .form-group label {
  margin: 0;
}

.actions .form-group input {
  color: #0fc;
  background-color: #333;
  width: 60px;
  height: 24px;
  padding: 2px 4px;
}

.scu-container {
  text-align: center;
  flex-direction: column;
  align-items: center;
  gap: 5px;
  display: flex;
}

.scu-label {
  color: #0fc;
  white-space: pre-wrap;
  margin-left: 10px;
  font-size: 15px;
}

.scu-total {
  color: #0fc;
  font-size: 14px;
}

.amount-input {
  color: #0fc;
  background-color: #333;
  border: 1px solid #333;
  border-radius: 5px;
  width: 30px;
  height: 20px;
  padding: 10px;
}

.capture-key-settings {
  align-items: center;
  gap: 10px;
  margin: 10px 0;
  display: flex;
}

.capture-key-settings input {
  border: 1px solid var(--button-color);
  width: 100px;
  color: var(--dropdown-text-color);
  background: #333;
  border-radius: 3px;
  padding: 5px;
}

.capture-key-settings button {
  white-space: nowrap;
}

.selection-box {
  border: 2px solid var(--button-color);
  pointer-events: none;
  background-color: #0fc3;
  position: absolute;
}

.saved-selection-box {
  border: 2px dashed var(--button-color);
  pointer-events: none;
  background-color: #00ffcc1a;
  position: absolute;
}

.capture-table-group {
  border: 1px solid var(--table-outline-color);
  border-radius: 5px;
  margin-bottom: 20px;
  overflow: hidden;
}

.capture-tab-header {
  border: 1px solid var(--table-outline-color);
  background-color: #1a1a1a;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  display: flex;
}

.capture-tab-header h5 {
  color: var(--mission-text-color);
  margin: 0;
}

.ocr-mission-payout-capture {
  width: 100px;
  color: var(--row-text-color);
  background-color: #333;
  border: 1px solid #444;
  border-radius: 4px;
  padding: 5px;
}

.date-group {
  border: 1px solid var(--table-outline-color);
  border-radius: 5px;
  margin-bottom: 30px;
  overflow: hidden;
}

.date-group h3 {
  color: var(--title-color);
  border-bottom: 1px solid var(--table-outline-color);
  background-color: #262626;
  margin: 0;
  padding: 15px;
}

.mission-entry {
  border: 1px solid var(--table-outline-color);
  background-color: #262626;
  border-radius: 5px;
  margin-bottom: 15px;
  overflow: hidden;
}

.mission-header {
  cursor: pointer;
  border-bottom: 1px solid var(--table-outline-color);
  background-color: #1a1a1a;
  justify-content: space-between;
  align-items: center;
  padding: 10px 15px;
  display: flex;
}

.mission-header h4 {
  color: var(--mission-text-color);
  margin: 0;
}

.mission-summary {
  color: var(--row-text-color);
  gap: 20px;
  display: flex;
}

.mission-details {
  padding: 15px;
}

.mission-details table {
  outline: 1px solid var(--table-outline-color);
  border-collapse: collapse;
  background-color: #262626;
  width: 100%;
  margin-bottom: 0;
}

.mission-details th {
  color: var(--table-header-text-color);
  text-align: left;
  border-bottom: 1px solid var(--table-outline-color);
  outline: 1px solid var(--table-outline-color);
  background-color: #1a1a1a;
  padding: 10px;
}

.mission-details td {
  border-bottom: 1px solid var(--table-outline-color);
  color: var(--row-text-color);
  outline: 1px solid var(--table-outline-color);
  padding: 8px 10px;
}

.mission-details tr:last-child td {
  border-bottom: none;
}

.date-total {
  color: var(--mission-text-color);
  text-align: right;
  border: 1px solid var(--table-outline-color);
  background-color: #262626;
  border-radius: 5px;
  margin-top: 15px;
  padding: 15px;
  font-weight: bold;
}

.scu-total-container {
  align-items: center;
  gap: 10px;
  margin: 10px 0;
  padding: 5px;
  display: flex;
}

.scu-total-container label {
  color: var(--dropdown-label-color);
  font-family: var(--main-font), sans-serif;
  font-size: 14px;
}

.scu-total-input {
  color: var(--dropdown-text-color);
  font-family: var(--main-font), sans-serif;
  text-align: right;
  background-color: #333;
  border: 1px solid #444;
  border-radius: 4px;
  width: 40px;
  padding: 5px 10px;
  font-size: 14px;
}

.table-container {
  border: 2px solid var(--table-outline-color);
  border-radius: 5px;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.table {
  border-collapse: separate;
  border-spacing: 0;
  background-color: #1a1a1a;
  width: 100%;
  margin: 0;
}

.table th, .table td {
  border: 1px solid var(--table-outline-color);
  padding: 10px;
}

.table th.pickup, .table td.pickup {
  text-align: center;
  width: 370px;
  color: var(--pickup-text-color);
  padding: 5px;
  font-size: 14px;
}

.table th.commodity, .table td.commodity {
  text-align: center;
  width: 160px;
  color: var(--commodity-text-color);
}

.table th.amount, .table td.amount {
  text-align: center;
  width: 100px;
  color: var(--amount-text-color);
  font-size: 14px;
}

.table th.actions, .table td.actions {
  text-align: center;
}

.table th.status, .table td.status {
  text-align: center;
  width: 90px;
}

.table td:not(.status):not(.actions) {
  color: var(--row-text-color);
}

.table th, .table th.pickup, .table th.commodity, .table th.amount {
  color: var(--table-header-text-color);
}

.table td.pickup, .table td.commodity, .table td.amount {
  color: var(--row-text-color);
}

.table .actions button {
  background-color: var(--button-color);
  color: #0d0d0d;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 13px;
  transition: background-color .3s, color .3s;
}

.table .actions button:hover {
  background-color: #0c9;
}

.table .actions .remove-cargo-button {
  color: #0d0d0d;
  background-color: #f66;
  font-size: 12px;
}

.table .actions .remove-cargo-button:hover {
  background-color: #f33;
}

.table .actions input[type="text"] {
  color: #0fc;
  background-color: #333;
  border: 1px solid #333;
  border-radius: 5px;
  width: 40px;
  padding: 5px;
  font-family: Orbitron, sans-serif;
}

.hauling-manifest-table {
  border: 2px solid var(--table-outline-color);
  border-collapse: separate;
  border-spacing: 0;
  border-radius: 5px;
  width: 100%;
  margin: 10px 0;
}

.hauling-manifest-table th, .hauling-manifest-table td {
  border: 1px solid var(--table-outline-color);
  padding: 8px;
}

.hauling-manifest-table th {
  color: var(--table-header-text-color);
  background-color: #1a1a1a;
  font-weight: bold;
}

.mission-table {
  border: 2px solid var(--table-outline-color);
  border-collapse: separate;
  border-spacing: 0;
  border-radius: 5px;
  width: 100%;
  margin: 10px 0;
}

.mission-table th, .mission-table td {
  border: 1px solid var(--table-outline-color);
  padding: 8px;
}

.mission-table th {
  color: var(--table-header-text-color);
  background-color: #1a1a1a;
  font-weight: bold;
}

.table-header {
  color: var(--drop-off-header-text-color);
  border-bottom: 2px solid var(--table-outline-color);
  background-color: #1a1a1a;
  padding: 10px;
  font-weight: bold;
}

.hauling-manifest-table td, .mission-table td {
  color: var(--row-text-color);
}

.mission-header, .mission-info {
  color: var(--mission-text-color);
}

.mission-group {
  border: 2px solid var(--table-outline-color);
  background-color: #1a1a1a;
  border-radius: 5px;
  margin-bottom: 20px;
  overflow: hidden;
}

.mission-header {
  border-bottom: 2px solid var(--table-outline-color);
  color: var(--mission-text-color);
  background-color: #1a1a1a;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  display: flex;
}

.table thead tr {
  border-bottom: 2px solid var(--table-outline-color);
  background-color: #1a1a1a;
}

.table thead th {
  border-top: none;
  border-bottom: 2px solid var(--table-outline-color);
  color: var(--table-header-text-color);
  font-weight: bold;
}

.table tr td:first-child, .table tr th:first-child {
  border-left: none;
}

.table tr td:last-child, .table tr th:last-child {
  border-right: none;
}

.table tr:last-child td {
  border-bottom: none;
}

.section-title {
  color: var(--dropdown-text-color);
  border-bottom: 2px solid var(--table-outline-color);
  margin-bottom: 5px;
  padding: 10px;
}

.input-section {
  border: 2px solid var(--table-outline-color);
  background-color: #1a1a1a;
  border-radius: 5px;
  margin-bottom: 15px;
  padding: 10px;
}

.title-row {
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  margin-bottom: 10px;
  display: grid;
}

.location-row {
  grid-template-columns: auto 1fr 1fr 1.5fr;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
  display: grid;
}

.commodity-row {
  grid-template-columns: 1fr auto;
  align-items: center;
  gap: 20px;
  margin-bottom: 10px;
  display: grid;
}

.title-label {
  color: var(--dropdown-text-color);
  border-bottom: 2px solid var(--table-outline-color);
  padding: 5px 0;
  font-weight: bold;
}

.section-divider {
  border-left: 2px solid var(--table-outline-color);
  height: 100%;
  margin: 0 10px;
}

.mission-selection {
  border: 2px solid var(--table-outline-color);
  background-color: #0003;
  border-radius: 5px;
  padding: 10px;
}

.preferences {
  color: #0fc;
  text-align: left;
}

.preferences .form-group {
  align-items: flex-start;
}

.preferences input[type="color"] {
  border-radius: 0;
  width: 50px;
  height: 20px;
  margin-left: 10px;
}

.preferences-box {
  background-color: #1a1a1a;
  border: 1px solid #333;
  border-radius: 10px;
  width: 250px;
  margin-bottom: 20px;
  padding: 20px;
  transition: background-color .3s, border-color .3s;
  box-shadow: 0 4px 8px #0003;
}

.preferences-box:hover {
  background-color: #333;
  border-color: #0fc;
}

.preferences-box h3 {
  border-bottom: 2px solid #0fc;
  padding-bottom: 5px;
}

.preferences-container {
  background-color: #1a1a1a;
  border: 1px solid #333;
  border-radius: 10px;
  flex-direction: row;
  gap: 20px;
  margin-bottom: 20px;
  padding: 20px;
  display: flex;
  box-shadow: 0 4px 8px #0003;
}

.preferences-box input[type="file"] {
  display: none;
}

.preferences-box label[for="file-upload"] {
  background-color: var(--button-color);
  color: #0d0d0d;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: 7px 20px;
  font-size: 16px;
  transition: background-color .3s, color .3s;
  display: inline-block;
}

.preferences-box label[for="file-upload"]:hover {
  background-color: #0c9;
}

.preferences-box button.export-history-button {
  background-color: var(--button-color);
  color: #0d0d0d;
  cursor: pointer;
  white-space: nowrap;
  text-align: center;
  border: none;
  border-radius: 5px;
  width: auto;
  min-width: 180px;
  margin-top: 10px;
  padding: 10px 10px 7px;
  font-size: 16px;
  transition: background-color .3s, color .3s;
  display: block;
}

.preferences-box button.export-local-storage-button {
  background-color: var(--button-color);
  color: #0d0d0d;
  cursor: pointer;
  white-space: nowrap;
  text-align: center;
  border: none;
  border-radius: 5px;
  width: auto;
  min-width: 180px;
  margin-top: 10px;
  padding: 7px 10px;
  font-size: 16px;
  transition: background-color .3s, color .3s;
  display: block;
}

.preferences-box button.clear-history-log-button {
  background-color: var(--button-color);
  color: #0d0d0d;
  cursor: pointer;
  border: none;
  padding: 7px 10px;
  font-size: 16px;
}

.preferences-box button.reset-welcome-button {
  background-color: var(--button-color);
  color: #0d0d0d;
  cursor: pointer;
  white-space: nowrap;
  text-align: center;
  border: none;
  border-radius: 5px;
  width: auto;
  margin-top: 15px;
  padding: 7px 10px;
  font-size: 16px;
  transition: background-color .3s, color .3s;
  display: block;
}

.preferences-box button.export-payouts-button {
  background-color: var(--button-color);
  color: #0d0d0d;
  cursor: pointer;
  white-space: nowrap;
  text-align: center;
  border: none;
  border-radius: 5px;
  width: auto;
  min-width: 180px;
  margin-top: 10px;
  padding: 10px 10px 7px;
  font-size: 16px;
  transition: background-color .3s, color .3s;
  display: block;
}

.font-options {
  flex-direction: column;
  gap: 1rem;
  margin: 1rem 0;
  display: flex;
}

.font-option {
  background: #0003;
  border-radius: 4px;
  align-items: center;
  gap: 1rem;
  padding: .5rem;
  display: flex;
}

.font-option:hover {
  background: #0000004d;
}

.font-option label {
  min-width: 150px;
  color: var(--dropdown-text-color);
}

.font-preview {
  color: var(--dropdown-text-color);
  opacity: .8;
  font-size: .9em;
}

.font-option input[type="radio"] {
  appearance: none;
  border: 2px solid var(--dropdown-text-color);
  cursor: pointer;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  margin: 0;
  position: relative;
}

.font-option input[type="radio"]:checked:after {
  content: "";
  background-color: var(--dropdown-text-color);
  border-radius: 50%;
  width: 8px;
  height: 8px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.font-option input[type="radio"]:hover {
  border-color: var(--button-color);
}

.welcome-popup {
  z-index: 1000;
  background-color: #00000080;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.welcome-popup-content {
  text-align: center;
  background-color: #1a1a1a;
  border-radius: 10px;
  max-width: 600px;
  padding: 20px;
  box-shadow: 0 4px 8px #0003;
}

.welcome-popup-content h2 {
  color: #0fc;
  margin-bottom: 10px;
  font-size: 1.5em;
}

.welcome-message-underline {
  border-bottom: 2px solid #0fc;
  margin-bottom: 10px;
}

.welcome-popup-content p {
  color: #0fc;
  margin-bottom: 15px;
  font-size: 1.2em;
  line-height: 1.6;
}

.welcome-popup-content button {
  background-color: var(--button-color);
  color: #0d0d0d;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s, color .3s;
}

.welcome-popup-content button:hover {
  background-color: #0c9;
}

.history-container {
  background-color: var(--secondary-background);
  border: 1px solid var(--border-color);
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 4px 8px #0003;
}

.history-group-box {
  flex-direction: column;
  gap: 15px;
  display: flex;
}

.history-date-group {
  background-color: var(--secondary-background);
  border: 1px solid var(--table-outline-color);
  border-radius: 10px;
  overflow: hidden;
}

.history-date-header {
  color: var(--drop-off-header-text-color);
  cursor: pointer;
  background-color: #333;
  padding: 10px;
  font-size: 16px;
  font-weight: bold;
  transition: background-color .3s;
}

.history-date-header:hover {
  background-color: #444;
}

.drop-off-group-history {
  flex-direction: column;
  gap: 2px;
  padding: 2px;
  display: flex;
}

.drop-off-header-history-header {
  border: 1px solid var(--border-color);
  cursor: pointer;
  background-color: #333;
  border-radius: 5px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  padding: 10px;
  font-size: 22px;
  transition: background-color .3s;
  display: flex;
}

.drop-off-header-history-header:hover {
  background-color: #444;
}

.history-table {
  border-collapse: separate;
  background-color: var(--secondary-background);
  border: 1px solid var(--table-outline-color);
  border-radius: 5px;
  width: 100%;
  overflow: hidden;
}

.history-table th, .history-table td {
  text-align: left;
  border: 1px solid var(--table-outline-color);
  padding: 6px;
}

.history-table th {
  color: var(--table-header-text-color);
